@import url(https://fonts.googleapis.com/css?family=Exo:100);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
.logo-container{font-family:'Montserrat', sans-serif;display:flex;align-items:center;justify-content:center;z-index:20}

.fa-bars{display:inherit}@media only screen and (min-width: 640px){.fa-bars{display:none}}

.fa-times{position:absolute;top:30px;right:30px}

.options-container{font-family:'Roboto Slab', serif;display:none;position:absolute;width:100%;height:200px;margin:0;padding:0;top:0px;left:0;flex-direction:column;align-items:center;justify-content:center;list-style-type:none;background-color:white;border-bottom:1px solid #ccc}.options-container.active{background-color:#ccc}.options-container .option{cursor:pointer}.options-container.show{display:flex}.options-container li{margin-bottom:30px}.options-container li a{color:inherit;text-decoration:none}@media only screen and (min-width: 640px){.options-container{width:auto;height:auto;display:flex;margin:0;padding-top:20px;position:static;flex-direction:row;background-color:transparent;border-bottom:none}.options-container .option{margin:0 20px}.options-container li{margin:0}.options-container li:first-child{display:none}}

.header-container{position:fixed;box-sizing:border-box;width:100%;display:flex;flex-wrap:wrap;padding:30px;align-items:center;justify-content:space-between;font-weight:bold;font-size:medium;z-index:10}.header-container.active{background-color:#ccc;border-bottom:1px solid #ccc;color:white;transition:0.7s}@media only screen and (min-width: 640px){.header-container{font-size:large}}

.footer-container{display:flex;flex-direction:column;align-items:center;text-align:center;color:#8c8c8c}.footer-container .logos-container{width:100%;align-items:center;text-align:center}.footer-container .logos-container svg{margin:0 20px}

.project-container{min-width:90%;height:180px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;overflow:hidden}.project-container:hover{cursor:pointer}.project-container:hover.background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.project-container .background-image{width:100%;height:100%;opacity:0.9;background-position:center;background-size:cover}.project-container .content{height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:2px solid black;background-color:white;opacity:0.8;position:absolute}.project-container .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:black}@media only screen and (min-width: 640px){.project-container{min-width:45%;height:200px}}@media only screen and (min-width: 1024px){.project-container{min-width:30%;height:220px}}

h5{margin:0;color:#282C35}.side-container{order:3;flex:1 1;text-align:left}@media only screen and (min-width: 640px){.side-container{order:1}}

.button{display:flex;flex-direction:row;justify-content:center;background-color:#2b2b2b;color:white;align-items:center;margin:0 20px;padding:5px;border:1px solid black;border-radius:4px;text-decoration:none}.button:hover{background-color:black}@media only screen and (min-width: 640px){.button{margin:0}}

.ReactModal__Overlay{font-family:'Roboto Slab', serif;z-index:100}.ReactModal__Overlay h2{font-weight:bold;color:black}.close-button{position:absolute;border:none;background-color:inherit;font-size:30px;top:20px;right:20px;cursor:pointer}.button-container{order:2;display:flex;flex-direction:row;width:100%;justify-content:space-evenly}.content-container{display:flex;flex-direction:column}.content-container p{text-align:left}@media only screen and (min-width: 640px){.button-container{order:3}.content-container{flex-direction:row;margin-top:20px}.content-container .main-container{flex:3 1;order:2;padding-left:40px}}


.directory-container{width:100%;padding:100px 0;display:flex;flex-wrap:wrap;justify-content:space-between}

.about-container{width:100%;height:300px;margin-top:100px;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#7f7f7f}.about-container h2{margin:0}.about-container .text-1{animation:text1}.about-container .text-2{animation:text2}.about-container .text-3{animation:text3}.about-container .text-1,.about-container .text-2,.about-container .text-3{top:7px;overflow:hidden;white-space:nowrap;display:inline-block;position:relative;animation-duration:10s;animation-timing-function:steps(25, end);animation-iteration-count:infinite}.about-container .text-1:after,.about-container .text-2:after,.about-container .text-3:after{content:'|';position:absolute;right:0;animation:caret infinite;animation-duration:1s;animation-timing-function:steps(1, end)}@keyframes caret{0%,100%{opacity:0}50%{opacity:1}}@keyframes text3{0%,33%,66%,100%{width:0}76%,90%{width:12.4em}}@keyframes text2{0%,33%,66%,100%{width:0}43%,56%{width:7.8em}}@keyframes text1{0%,33%,66%,100%{width:0}10%,23%{width:8.4em}}@media only screen and (min-width: 640px){.about-container{height:400px}}

.arrow{text-align:center;margin:70px 0;cursor:pointer}.arrow span{font-size:350%;font-weight:bolder}.arrow.bounce{animation:bounce 3s infinite}@keyframes bounce{0%,20%,50%,80%,100%{transform:translateY(0)}40%{transform:translateY(-30px)}60%{transform:translateY(-15px)}}@media (max-width: 500px) and (orientation: landscape){.arrow{margin:0}}

.homepage-container{display:flex;flex-direction:column;align-items:center;padding:10px 40px}@media only screen and (min-width: 640px){.homepage-container{padding:20px 25px}}@media only screen and (min-width: 1024px){.homepage-container{padding:20px 50px}}

head,body{box-sizing:border-box}

@keyframes bg-scrolling-reverse{100%{background-position:50px 50px}}@keyframes bg-scrolling{0%{background-position:50px 50px}}body{color:#999;font-family:'Orbitron', sans-serif;text-align:center;background:url("data:image/png;base64,") repeat 0 0;animation:bg-scrolling-reverse .92s infinite;animation-timing-function:linear}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

